
.payment .mint-header {
  background-color: #E35353;
}
.payment .payMethod {
  margin: 0;
  padding: 10px;
  background-color: #F5F5F5;
}
.payment .warnings {
  font-size: 0.75em;
  background-color: #F5F5F5;
  color: #E35353;
  margin: 0;
  padding: 10px;
  line-height: 1.15;
}
.payment section > p {
  padding: 10px;
  margin: 0;
}
.payment section > p span {
    display: inline-block;
}
.payment section > p span:first-child {
      width: 5em;
}
.payment section > p span:last-child {
      color: #E35353;
}
.payment section h4 {
  text-align: center;
}
.payment section .banks > p {
  padding-left: 10px;
}
.payment section .banks p {
  color: #E35353;
}
.payment section .banks > div {
  box-sizing: border-box;
  padding-left: 5em;
}
.payment section .banks .bankCart {
  float: left;
  padding: 10px 15px;
  margin: 10px;
  box-sizing: border-box;
  border: 1px solid #E35353;
  border-radius: 5px;
}
.payment section .banks .bankActive {
  background-color: #E35353;
  color: #fff;
}



