
.home {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  background-color: #E35353;
  box-sizing: border-box;
  padding: 80px 0 80px 0;
}
.home header {
    position: absolute;
    text-align: center;
    padding: 10px 0;
    width: 100%;
    top: 0;
    left: 0;
    color: #fff;
}
.home header span {
      font-size: 1.3em;
}
.home section {
    height: 100%;
    width: 100%;
    position: relative;
    color: black;
}
.home section div {
      box-sizing: border-box;
      padding: 40px 20px;
      background-color: #fff;
      min-width: 200px;
      position: absolute;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      top: 50%;
      left: 50%;
}
.home section article {
      text-indent: 2em;
      word-wrap: break-word;
      word-break: break-all;
      text-decoration: underline;
      line-height: 1.5;
}
.home section p {
      margin: 0;
}
.home footer {
    position: absolute;
    bottom: 0px;
    left: 0px;
}
.home footer .mint-button {
      background-color: #FFDB22;
      color: #9E5932;
}



