
.order .mint-header {
  background-color: #E35353;
}
.order .order-info .order-info__details {
  box-sizing: border-box;
  padding: 0 20px;
}
.order .order-info .right {
  float: right;
}
.order .order-info .info__details-text {
  box-sizing: border-box;
  padding: 0 20px;
}
.order .order-info .order-info__applicant--id {
  position: relative;
}
.order .order-info .order-info__applicant--id .posiRight {
    position: absolute;
    right: 20px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    bottom: 0;
}
.order .order-info .switch {
  font-size: 16px;
  box-sizing: border-box;
  border-top: 1px solid #d9d9d9;
  padding: 5px 0;
  margin-left: 10px;
}
.order .order-info .switch > label, .order .order-info .switch > span {
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
}
.order .order-info .switch > span {
    margin-right: 30px;
}
.order .order-info .switch .mint-switch-core {
    vertical-align: middle;
}
.order .order-info .switch .mint-switch-label {
    vertical-align: middle;
}
.order .order-info .checklist {
  font-size: 16px;
  position: relative;
}
.order .order-info .checklist > a {
    position: absolute;
    left: 120px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    text-decoration: none;
}
.order .title {
  margin: 0;
  padding: 10px 20px;
  background-color: #F5F5F5;
}
.info__details-enter-active, .info__details-leave-active {
  transition: all .3s ease;
}
.info__details-enter, .info__details-leave-active {
  -webkit-transform: translateX(-300px);
          transform: translateX(-300px);
}



